.Contact {
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #6B7C93;
  background-blend-mode: multiply;
  mix-blend-mode: normal;
  opacity: 0.84;
  padding: 6.25rem 0;
  text-align: center;
}
.Contact__heading {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1.75rem;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2.125rem;
}
@media screen and (max-width: 720px) {
  .Contact__heading {
    line-height: 1.344em;
    font-size: 2rem;
  }
}
.Contact__form {
  max-width: 32.25rem;
  margin: auto;
}
.Contact__form-field {
  display: block;
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #DADFE9;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 15px;
}
.Contact__form-field::-webkit-input-placeholder {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1rem;
  color: #9BA9BB;
}
.Contact__form-field:not(:last-child) {
  margin-bottom: 0.625rem;
}
.Contact__btn-submit {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1.25rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #FF7043;
  padding: 0.75em 0;
  width: 240px;
  box-shadow: 0px 2px 2px rgba(127, 32, 2, 0.475175);
  -webkit-border-radius: 5px;
  -webkit-background-clip: padding-box;
  -moz-border-radius: 5px;
  -moz-background-clip: padding;
  border-radius: 5px;
  background-clip: padding-box;
  width: 100%;
  margin-top: 0.75rem;
}
@media screen and (max-width: 720px) {
  .Contact__btn-submit {
    font-size: 1.25rem;
  }
}
@media screen and (max-width: 920px) {
  .Contact__content {
    max-width: 520px;
    margin: auto;
  }
}
@media screen and (max-width: 720px) {
  .Contact {
    padding: 3.125rem 0;
  }
  .Contact__content {
    width: 85%;
    margin: auto;
  }
  .Contact__heading {
    margin-bottom: 0.625em;
  }
}
