.Footer {
  background-color: #FFFFFF;
  padding: 3.75rem 0;
}
.Footer__content {
  max-width: 1140px;
  margin: auto;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.Footer__logo {
  margin-right: 1.875em;
}
.Footer__copyright {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 0.875rem;
  color: #6B7C93;
  margin-right: 3.5em;
  min-width: 10em;
}
.Footer__nav {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-grow: 2;
  flex-grow: 2;
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.Footer__nav-list {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.Footer__nav-item {
  -webkit-flex-grow: 2;
  flex-grow: 2;
}
.Footer__nav-item:not(:last-child) {
  margin-right: 1.25rem;
}
.Footer__social-media {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.Footer__sm-icon:not(:last-child) {
  margin-right: 2.5rem;
}
.Footer__nav-link {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1rem;
  color: #039BE5;
}
@media screen and (max-width: 1200px) {
  .Footer {
    width: 85%;
    margin: auto;
    padding: 2.5rem 0;
  }
  .Footer__content {
    display: block;
    text-align: center;
  }
  .Footer__logo {
    margin-right: 0;
    margin-bottom: 1.6875rem;
  }
  .Footer__copyright {
    margin-right: 0;
    margin-bottom: 1.25rem;
  }
  .Footer__nav {
    display: block;
    margin-bottom: 1rem;
  }
  .Footer__nav-list {
    display: block;
  }
  .Footer__nav-item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 1.125rem;
  }
  .Footer__social-media {
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
  }
}
