.HeaderDesktop {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding-top: 0.875em;
  max-width: 1140px;
  margin: auto;
}
.HeaderDesktop__logo {
  margin-right: 2.5em;
}
.HeaderDesktop__nav {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-flex-grow: 2;
  flex-grow: 2;
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1rem;
  color: #FFFFFF;
}
.HeaderDesktop__nav-left,
.HeaderDesktop__nav-right {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.nav__item:not(:last-child) {
  margin-right: 3.25em;
}
.nav__item_dropdown {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.nav__dropdown-icon {
  font-family: Material Icons;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1em;
  padding-top: 0.125em;
  color: #FFFFFF;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.nav__item_button-style {
  padding: 0.5em 2em;
  border-radius: 0.25em;
  border: 1px solid white;
}
