.Solutions {
  max-width: 1140px;
  margin: auto;
  padding: 6.375rem 4.5rem 0;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.Solutions__heading,
.Solutions__features {
  width: 50%;
}
.Solutions__heading {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  font-size: 3.875em;
  letter-spacing: 1px;
  color: #039BE5;
}
@media screen and (max-width: 1200px) {
  .Solutions__heading {
    font-size: 3.125rem;
    line-height: 1.4em;
  }
}
@media screen and (max-width: 920px) {
  .Solutions__heading {
    font-size: 2rem;
    line-height: 1.34375em;
    letter-spacing: 0.580645px;
  }
}
.Solutions__features-item {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: flex-start;
  padding-left: 5rem;
}
.Solutions__features-item:not(:last-child) {
  margin-bottom: 3.875rem;
}
.Solutions__features-box {
  margin-left: 2.75rem;
}
.Solutions__features-icon_desktop {
  margin-top: 0.5rem;
}
@media screen and (min-width: 1201px) {
  .Solutions__features-icon_mob {
    display: none;
  }
}
.Solutions__features-heading-text {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  font-size: 1.5rem;
  letter-spacing: 0.74px;
  color: #6B7C93;
  margin-bottom: 0.833rem;
}
@media screen and (max-width: 720px) {
  .Solutions__features-heading-text {
    font-size: 1.25rem;
  }
}
.Solutions__features-description {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: 2em;
  font-size: 1rem;
  letter-spacing: 0.74px;
  color: #6B7C93;
}
@media screen and (max-width: 1200px) {
  .Solutions__heading {
    width: 42%;
  }
  .Solutions__features {
    width: 58;
  }
  .Solutions__features-icon_desktop {
    display: none;
  }
  .Solutions__features-icon_mob {
    -webkit-transform: scale(0.55);
    -moz-transform: scale(0.55);
    -ms-transform: scale(0.55);
    -o-transform: scale(0.55);
    transform: scale(0.55);
  }
  .Solutions__features-heading {
    display: -webkit-box;
    display: -moz-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -moz-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    margin-bottom: 0.75em;
  }
  .Solutions__features-heading-text {
    margin-bottom: 0;
  }
  .Solutions__features-item {
    padding-left: 2rem;
  }
  .Solutions__features-box {
    margin: auto;
  }
}
@media screen and (max-width: 920px) {
  .Solutions {
    display: block;
    max-width: 520px;
    margin: auto;
    padding: 4.5rem 0 0;
    text-align: center;
  }
  .Solutions__heading,
  .Solutions__features {
    width: 100%;
  }
  .Solutions__heading {
    margin-bottom: 1.5rem;
  }
  .Solutions__features-item {
    padding: 0;
  }
  .Solutions__features-item:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
@media screen and (max-width: 720px) {
  .Solutions {
    padding: 2.375rem 0 0;
  }
}
@media screen and (max-width: 640px) {
  .Solutions {
    width: 85%;
    max-width: none;
  }
}
