.Banner {
  max-width: 1140px;
  margin: auto;
  text-align: center;
  padding: 5.75rem 0;
}
.Banner__heading {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  font-size: 3.125rem;
  text-align: center;
  letter-spacing: 1px;
  color: #fff;
}
@media screen and (max-width: 720px) {
  .Banner__heading {
    font-size: 2.75rem;
  }
}
.Banner__description {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: 1.583em;
  font-size: 1.5rem;
  text-align: center;
  letter-spacing: 1px;
  color: #FFFFFF;
  margin: auto auto 1.667rem;
  width: 70%;
}
@media screen and (max-width: 720px) {
  .Banner__description {
    font-weight: normal;
    line-height: 1.375em;
    font-size: 1rem;
  }
}
.Banner__button {
  display: inline-block;
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1.25rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #FF7043;
  padding: 0.75em 0;
  width: 240px;
  box-shadow: 0px 2px 2px rgba(127, 32, 2, 0.475175);
  -webkit-border-radius: 5px;
  -webkit-background-clip: padding-box;
  -moz-border-radius: 5px;
  -moz-background-clip: padding;
  border-radius: 5px;
  background-clip: padding-box;
  margin-bottom: 1.9rem;
}
@media screen and (max-width: 720px) {
  .Banner__button {
    font-size: 1.25rem;
  }
}
.Banner__example-img {
  max-width: 80%;
}
@media screen and (max-width: 720px) {
  .Banner {
    padding: 1.625rem 0 3.125rem;
  }
  .Banner__heading {
    margin-bottom: 0.625rem;
  }
  .Banner__description {
    margin: auto;
    width: 85%;
    margin-bottom: 1.625em;
  }
  .Banner__example-img {
    max-width: 92%;
  }
}
