.Policies {
  background-color: #FFFFFF;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 20%;
}
.Policies__content {
  max-width: 1140px;
  margin: auto;
  text-align: center;
  padding: 0 4.5rem 6.375rem 4.5rem;
}
.Policies__heading {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: 1.227em;
  font-size: 2.75rem;
  text-align: center;
  letter-spacing: 1px;
  color: #FFFFFF;
  margin-bottom: 1.545em;
}
@media screen and (max-width: 720px) {
  .Policies__heading {
    line-height: 1.344em;
    font-size: 2rem;
  }
}
.Policies__row {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-pack: justify;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.Policies__row:not(:last-child) {
  margin-bottom: 3.125rem;
}
.Policies__row_one-item {
  justify-content: center;
}
.Policies__box {
  background-color: #FFFFFF;
  box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.209352);
  border-radius: 4px;
  width: 50%;
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 2.188rem;
}
.Policies__box_right {
  margin-left: 1.563rem;
}
.Policies__box_left {
  margin-right: 1.563rem;
}
.Policies__box-icon {
  margin-bottom: 0.875rem;
}
.Policies__box-text {
  padding-left: 1.75rem;
  padding-top: 0.625rem;
}
.Policies__box-heading {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  font-size: 1rem;
  letter-spacing: 0.74px;
  color: #039BE5;
  text-align: left;
  margin-bottom: 0.875rem;
}
.Policies__box-description {
  font-family: 'Lato', sans-serif;
  font-style: normal;
  font-weight: normal;
  line-height: 1.313em;
  font-size: 1rem;
  color: #6B7C93;
  text-align: left;
}
@media screen and (max-width: 1200px) {
  .Policies {
    background-position-x: calc(90%);
  }
}
@media screen and (max-width: 920px) {
  .Policies {
    background-size: auto 120%;
    background-position-x: calc(70%);
    background-position-y: calc(100% + 235.8px);
  }
  .Policies__content {
    display: block;
    max-width: 520px;
    margin: auto;
    padding: 6.25rem 0 3.125rem;
  }
  .Policies__heading {
    margin-bottom: 1.875rem;
  }
  .Policies__row {
    display: block;
  }
  .Policies__row:not(:last-child) {
    margin-bottom: 0;
  }
  .Policies__box {
    display: block;
    width: 100%;
  }
  .Policies__box_right {
    margin: auto;
    margin-bottom: 1.625rem;
  }
  .Policies__box_left {
    margin: auto;
    margin-bottom: 1.625rem;
  }
  .Policies__box-text {
    padding: 0;
  }
  .Policies__box-heading {
    text-align: center;
  }
  .Policies__box-description {
    text-align: center;
  }
}
@media screen and (max-width: 720px) {
  .Policies__content {
    width: 85%;
    max-width: none;
  }
}
